ul{
    padding: 0;
    list-style: none;
}
.header{
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 100px;
    border: 1px solid white;
    display: flex;
    flex-direction: column;
}
.more{
    display: none;
}
.links{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
@media screen and (max-width: 375px) {
    .header{
        top: 0;
        right: 0;
        height: 70px;
        width: 100%;
        flex-direction: row;
        justify-content: space-between;
    }
    .slide{
        display: none;
    }
    .more{
        display: block;
    }
}
